import Vue from 'vue'
import Router from 'vue-router'
import Default from '@/layout/Default'
import Home from '@/pages/home/Home'
import AlarmMonitor from '@/pages/alarmMonitor/alarmMonitor'
import Report from '@/pages/report/Report'
import Setting from '@/pages/setting/Setting'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'default',
      component: Default,
      children:[{
          path: 'home',
          name: 'Home',
          component: Home
        },{
          path: 'alarmMonitor',
          name: 'AlarmMonitor',
          component: AlarmMonitor
        },{
          path: 'report',
          name: 'Report',
          component: Report
        },{
        path: 'setting',
        name: 'setting',
        component: Setting
      }]
    },
    {
      path:'*',
      redirect:'home'
    }
  ]
})



